import React, { useEffect, useState } from 'react'
import './style.css'
import * as bg_img from '../../../assets/bg_tmap.svg'

import HeaderComponent from 'components/Common/HeaderComponent';
import FooterComponent from 'components/Common/FooterComponent';
import Spinner from 'components/Common/Spinner';

import { useColourObjectStore } from '../../../store/ColourStore';


const ScreenTemplate = (props: any) => {

  const { spinner, instancedata, screen_name, title, main_body, footer, footer_body, Rewardscreen, setcompleted, isCompleted, aniout, setaniout } = props;

  const { colourObj } = useColourObjectStore();

  useEffect(() => {
    console.log("ST",aniout);
  }, [aniout])

  const fade = (props) => {
    let result = (aniout ? (props + " " + "fade_out") : (props))
    // console.log(result);
    return result
  }

  return (
    <div className="game bg">

      <div className={screen_name.includes("VIDEO") ? fade("game bg_color video") : "game bg_color"} style={screen_name.includes("VIDEO") ? { backgroundColor: colourObj.color_s2 } : {}} />

      <img className="bg_img" src={bg_img}></img>

      <HeaderComponent
        instancedata={instancedata}
        screen={screen_name}
        Rewardscreen={Rewardscreen}
        setcompleted={setcompleted}
        isCompleted={isCompleted}
        aniout={aniout}
        setaniout={setaniout}
      />

      {title !== undefined &&
        <div className='titletext' style={{ color: colourObj.color_m1, textDecorationColor: colourObj.color_m2 }}>{title}</div>
      }

      {spinner ?
        <div style={{
          width: '100vw',
          height: '100vh',
          position: 'absolute',
          top: '0',
          zIndex: 1000,
          backgroundColor: '#00243a',
        }} >
          <Spinner />
        </div >
        :
        main_body
      }

      {footer !== "False" &&
        <FooterComponent
          screen={screen_name}
          footer_body={footer_body}
          aniout={aniout}
          setaniout={setaniout}
        />
      }

    </div>
  )
}

export default ScreenTemplate
